import React from 'react'
import HomeIcon from '@material-ui/icons/Home';
import FeaturedPlayListOutlinedIcon from '@material-ui/icons/FeaturedPlayListOutlined';
import AssignmentIndOutlinedIcon from '@material-ui/icons/AssignmentIndOutlined';
import PeopleAltOutlinedIcon from '@material-ui/icons/PeopleAltOutlined';
import NotificationsOutlinedIcon from '@material-ui/icons/NotificationsOutlined';
import SearchOutlinedIcon from '@material-ui/icons/SearchOutlined';

import {Avatar, Input/*Button*/} from '@material-ui/core';
import "./css/QuoraHeader.css";
import CloseIcon from '@material-ui/icons/Close';
import { useState } from 'react';
import "react-responsive-modal/styles.css";
import Modal from 'react-responsive-modal';
import { ExpandMore, PeopleAltOutlined } from '@material-ui/icons';

function QuoraHeader() {

  const [isModalOpen, setIsModalOpen] = useState(false);
  const [inputUrl, setInputUrl] = useState("");
  const Close = <CloseIcon/>;

  return (
    <div className= "qHeader">
      <div className= "qHeader-content">
        <div className= "qHeader__logo">
          <img src='https://video-public.canva.com/VAD8lt3jPyI/v/ec7205f25c.gif' alt='logo'/>
        </div>
          <div className='qHeader__icons'>
            <div className='qHeader__icon'><HomeIcon/></div>
            <div className='qHeader__icon'><FeaturedPlayListOutlinedIcon/></div>
            <div className='qHeader__icon'><AssignmentIndOutlinedIcon/></div>
            <div className='qHeader__icon'><PeopleAltOutlinedIcon/></div>
            <div className='qHeader__icon'><NotificationsOutlinedIcon/></div>
          </div>
          <div className='qHeader__input'>
            <SearchOutlinedIcon/>
            <input type='text' placeholder='Search'/>
          </div>
          <div className='qHeader__Rem'><Avatar /></div>
          <button className='addQ' onClick={() => setIsModalOpen(true)}>Add Question</button>
          <Modal
          open={isModalOpen}
          closeIcon={Close}
          onClose={() => setIsModalOpen(false)}
          closeOnEsc
          center
          closeOnOverlayClick={false}
          styles={{
            overlay:{
              height: "auto",
            }
          }}
          >
            <div className='modal__title'>
              <h5>Add Question</h5>
              <h5>Share Link</h5>
            </div>
            <div className='modal__info'>
              <Avatar className='avatar'/>
              <div className='modal__scope'>
                <PeopleAltOutlined/>
                <p>Public</p>
                <ExpandMore/>
              </div>
            </div>
            <div className='modal__Field'>
              <Input type='text' placeholder="Start with your Question with 'What','How', 'why' etc.. " />
              <div className='modal__fieldLink'
                style={{
                  display: 'flex',
                  felxDirection: "column"
                }}
              >
                <input 
                value={inputUrl}
                onChange={(e) => setInputUrl(e.target.value)}
                style={{
                  margin: "5px 0px",
                  border: "1px solid lightgray",
                  padding: "10px",
                  outline: "2px solid #000",
                }} 
                type='text' placeholder='Optional: include a link that gives context.' />
              </div>
              {
                inputUrl !== "" && 
                (<img 
                  style={{
                    height: "40vh",
                    objectfit: "contain"
                  }}
                  src={inputUrl} alt="displayimage"/>
              )}
            </div>
            <div className='modal__buttons'>
              <button className='cancle' onClick={() => setIsModalOpen(false)}>
                cancel
              </button>
              <button type='submit' className='add'>
                Add Question
              </button>
            </div>
          </Modal>
      </div>
    </div>
  )
}

export default QuoraHeader